.nav-tabs {
	float: left;
	width: 100%;
	margin: 0;
	list-style-type: none;
	border-bottom: 1px solid transparent;
	background-color: #333;

	& > li {
		float: left;
		margin-bottom: -1px;

		& > a {
			margin-right: 2px;
			line-height: 1.5;
			padding: 10px;
			border: 1px solid transparent;
			border-radius: 4px 4px 0 0;
			float: left;
			text-decoration: none;

			&:hover {
				border-color: #eee #eee #ddd;
			}
		}

		&.active > a,
		.active > a:hover,
		.active > a:focus{
			color: #555;
			cursor: default;
			background-color: #fff;
			border-color: transparent;
		}
	}
}

.tab-content {
	& > .tab-pane {
		float: left;
		width: 100%;
		display: none;

		&.active {
			display: block;
			padding: 10px;
			background-color: #fff;
			box-shadow: 0 5px 4px -2px rgba(0, 0, 0, 0.15);
		}
	}
}